<template>
  <div class="account-detail-container">
    <div class="first-row">
      <div class="title-box">
        <span class="title">{{ title }}</span>
        <span class="desc" v-if="showDesc" @click="goDesc">?</span>
        <span class="detail" v-if="showDetail" @click="goDetail">明细</span>
      </div>
    </div>
    <div class="second-row" v-if="single">
      <div class="money-num">{{ amount }}</div>
    </div>
    <div class="second-row" v-else>
      <div class="left-box">
        <div class="money-name">现金余额（元）</div>
        <div class="money-num">{{ cash }}</div>
      </div>
      <div class="right-box" v-if="cooperateType == 1">
        <div class="money-name">积分余额（个）</div>
        <div class="money-num">{{ huanbi }}</div>
        <div class="withdrawal" v-if="needWithDrawal && cooperateType == 1" @click="goWithDrawal">提现消费</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: '',
  props: {
    title: null,
    showDesc: {
      type: Boolean,
      default: true
    },
    showDetail: {
      type: Boolean,
      default: true
    },
    single: {
      type: Boolean,
      default: false
    },
    needWithDrawal: {
      type: Boolean,
      default: false
    },
    amount: null,
    cash: null,
    huanbi: null
  },
  data() {
    return {}
  },
  computed: {
    ...mapState({
      cooperateType: state => state.suppliers.cooperateType
    })
  },
  created() {},
  methods: {
    goDesc() {
      this.$emit('show-desc')
    },
    goDetail() {
      this.$emit('go-detail')
    },
    goWithDrawal() {
      this.$emit('go-with-draw')
    }
  }
}
</script>

<style lang="scss" scoped>
.account-detail-container {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  padding: 30px 0;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  height: 200px;
  .first-row {
    text-align: center;
    span {
      display: inline-block;
    }
    .title {
      font-size: 20px;
      font-weight: bold;
    }
    .desc {
      border: 1px solid #41a5e1;
      border-radius: 50%;
      color: #41a5e1;
      width: 12px;
      height: 12px;
      line-height: 12px;
      margin-left: 2px;
      cursor: pointer;
    }
    .detail {
      margin-left: 15px;
      color: #41a5e1;
      cursor: pointer;
    }
  }
  .second-row {
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
    & > div {
      text-align: center;
    }
    .money-num {
      font-weight: bold;
      font-size: 18px;
    }
    .withdrawal {
      color: #41a5e1;
      cursor: pointer;
    }
  }
}
</style>
