<template>
  <div class="container">
    <page-title first-title="账户信息"></page-title>
    <suppliers-base-info :suppliersInfo="suppliers_info" :suppliersId="suppliers_id"></suppliers-base-info>
    <section>
      <div class="account-detail clearfix">
        <el-row :gutter="20">
          <el-col :span="6">
            <account-detail-item
              title="账户余额"
              :needWithDrawal="isPlatformRole"
              @show-desc="showDesc('account')"
              @go-detail="goDetail('account')"
              @go-with-draw="goWithdraw"
              :cash="account.cash_money"
              :huanbi="account.huanbi_money"
            ></account-detail-item>
          </el-col>
          <el-col :span="6">
            <account-detail-item
              title="待结算金额"
              @show-desc="showDesc('waitmoney')"
              @go-detail="goDetail('waitmoney')"
              :cash="account.wait_statement_cash"
              :huanbi="account.wait_statement_huanbi"
            ></account-detail-item>
          </el-col>
          <el-col :span="6">
            <account-detail-item
              title="交易中的金额"
              @show-desc="showDesc('salemoney')"
              :show-detail="false"
              :cash="account.trade_statement_cash"
              :huanbi="account.trade_statement_huanbi"
            ></account-detail-item>
          </el-col>
          <el-col :span="6">
            <account-detail-item
              title="保证金"
              @go-detail="goDetail('bond')"
              :show-desc="false"
              :single="true"
              :amount="account.bond_money"
            ></account-detail-item>
          </el-col>
        </el-row>
        <el-row :gutter="20" v-if="cooperateType == 1" style="margin-top: 20px;">
          <el-col :span="6">
            <account-detail-item
              title="消费积分账户"
              :show-detail="false"
              :show-desc="false"
              :single="true"
              :amount="account.huanbi_withdraw"
            ></account-detail-item>
          </el-col>
        </el-row>
      </div>
    </section>

    <div class="search-wrapper">
      <el-form :inline="true">
        <el-form-item label="日账单编号">
          <el-input
            v-model="params.statement_no"
            placeholder="请输入日账单编号"
            clearable
            style="width: 180px"
          ></el-input>
        </el-form-item>
        <el-form-item label="生成日期">
          <el-date-picker
            v-model="params.addtime"
            type="date"
            placeholder="选择开始日期"
            style="width:150px"
            value-format="timestamp"
          >
          </el-date-picker>
          -
          <el-date-picker
            v-model="params.endtime"
            type="date"
            placeholder="选择截至日期"
            style="width:150px"
            value-format="timestamp"
          >
          </el-date-picker>

          <div class="radio-wrapper">
            <el-radio-group size="small" v-model="timeselect" @change="selecttime">
              <el-radio-button v-for="(item, index) in timelist" :key="index" :label="item.value">{{
                item.name
              }}</el-radio-button>
            </el-radio-group>
          </div>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="params.status" clearable style="width: 140px">
            <el-option v-for="item in statusList" :key="item.id" :value="item.id" :label="item.name"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div>
        <el-button type="primary" @click="search">搜索</el-button>
        <el-button @click="clear">清空</el-button>
      </div>
    </div>

    <div class="list-wrapper">
      <div class="btn-box">
        <el-button type="primary" @click="exportList" v-if="!isSettle">导出账单详情</el-button>
        <el-button
          style="background-color: #13ce66;color: #fff;"
          @click="exportPay"
          v-if="isSettle"
          :disabled="suppliers_info.create_multi_method == 2"
          >生成对账单</el-button
        >
        <el-switch
          v-if="isPlatformRole"
          v-model="isSettle"
          active-color="#13ce66"
          inactive-color="#409eff"
          style="padding: 0 20px"
        ></el-switch>
        <span v-if="isPlatformRole && isSettle">切换导出账单</span>
        <span v-else-if="isPlatformRole && !isSettle">切换生成对账单</span>
      </div>
      <el-table
        :data="list"
        border
        style="width: 100%"
        stripe
        :header-cell-style="{ background: '#eef1f6' }"
        @select="handleSelection"
        @select-all="selectAll"
        ref="statementList"
        v-loading="isLoading"
      >
        <template>
          <el-table-column type="selection" key="multi" :selectable="filterSelect"></el-table-column>
        </template>
        <el-table-column prop="statement_no" label="日账单编号"></el-table-column>
        <el-table-column prop="addtime" label="日账单生成日期" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="add_cash" label="现金收入（元）"></el-table-column>
        <el-table-column prop="add_huanbi" label="积分收入（个）" v-if="cooperateType == 1"></el-table-column>
        <el-table-column prop="sub_cash" label="现金支出（元）"></el-table-column>
        <el-table-column prop="sub_huanbi" label="积分支出（个）" v-if="cooperateType == 1"></el-table-column>

        <el-table-column prop="cash" label="现金结算（元）"></el-table-column>
        <el-table-column prop="huanbi" label="积分结算（个）" v-if="cooperateType == 1"></el-table-column>
        <el-table-column prop="status" label="状态" :formatter="statusFormat"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="viewDetail(scope.row.suppliers_statement_id)" type="text" size="small">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="params.page"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
import {
  getSuppliersAccount,
  getSuppliersInfo,
  getSuppliersStatementList,
  getSuppliersMultiStatementList,
  exportList,
  statementPreview
} from '../../api/suppliers'
import { getRole } from '../../api/settle'
import { mapState, mapMutations } from 'vuex'
import SuppliersBaseInfo from '../../components/suppliers/SuppliersBaseInfo'
import AccountDetailItem from './child/AccountDetailItem'
import { PAY_METHODS, MULTI_STATEMENT_STATUS, STATEMENT_STATUS } from '../../const/status'
import { saveAs } from 'file-saver'
import { ACCOUNT_STATUS, TIMELIST } from './static'

export default {
  name: 'SuppliersAccount',
  data() {
    return {
      statusList: ACCOUNT_STATUS,
      // 时间筛选
      timelist: TIMELIST,
      timeselect: '',
      // 筛选条件
      params: {
        suppliers_id: '',
        statement_no: '',
        addtime: '',
        endtime: '',
        status: '',
        page: 1,
        size: 10
      },
      total: 0,
      list: [],
      suppliers_info: {
        suppliers_name: '',
        shop_name: '',
        manager_name: '',
        manager_tel: ''
      },
      account: {}, // 账户信息
      isLoading: false, //是否正在加载
      selectObj: {}, //导出结算单
      isSettle: false //导出or生成
    }
  },
  components: {
    SuppliersBaseInfo,
    AccountDetailItem
  },
  created() {
    this.getInfo()
    this.getStateList()
    this.getRoles()
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id,
      role: state => state.settle.role,
      cooperateType: state => state.suppliers.cooperateType
    }),
    isPlatformRole() {
      return this.role === 'all' || this.role === 'operater'
    },
    isSupplierRole() {
      return this.role === 'supplier'
    },
    // 格式化时间戳
    fmtParams() {
      let cloneParams = JSON.parse(JSON.stringify(this.params))
      if (cloneParams.addtime.toString().length > 10) {
        cloneParams.addtime = parseInt(cloneParams.addtime / 1000)
      }
      if (cloneParams.endtime.toString().length > 10) {
        cloneParams.endtime = parseInt(cloneParams.endtime / 1000)
      }
      cloneParams.suppliers_id = this.suppliers_id
      return cloneParams
    }
  },
  methods: {
    ...mapMutations({
      setSupplierName: 'setSupplierName',
      saveRole: 'saveRole',
      saveCooperateType: 'saveCooperateType',
      saveStatementIdList: 'saveStatementIdList',
      saveStatementPreview: 'saveStatementPreview'
    }),
    getRoles() {
      getRole().then(res => {
        this.saveRole(res.role)
      })
    },
    getInfo() {
      getSuppliersAccount(this.suppliers_id).then(res => {
        this.account = res
      })
      getSuppliersInfo(this.suppliers_id).then(res => {
        this.suppliers_info = res
        this.setSupplierName(res.suppliers_name)
        this.saveCooperateType(res.cooperate_type)
      })
    },
    getStateList() {
      this.isLoading = true
      getSuppliersStatementList(this.fmtParams)
        .then(res => {
          this.list = res.list
          this.total = res.paged.total
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    viewDetail(id) {
      this.$router.push({ name: 'SuppliersStatementInfo', params: { id: id } })
    },
    // 展示说明
    showDesc(type) {
      let desc = ''
      if (type == 'account') {
        desc =
          '现金余额：生成结算单后金额打入该账户，财务打款后金额从该账号扣除。<br>积分余额：生成结算单后积分打入该账号'
      } else if (type == 'waitmoney') {
        desc = '已确认收货未生成对账单的订单合计'
      } else if (type == 'salemoney') {
        desc = '已付款未确认收货前的订单合计'
      }
      this.$alert(desc, '', {
        dangerouslyUseHTMLString: true
      })
    },
    // 跳转明细
    goDetail(type) {
      let name = ''
      if (type == 'account') {
        name = 'accountBalance'
      } else if (type == 'waitmoney') {
        name = 'unsettledDetail'
      } else if (type == 'bond') {
        name = 'BondInfo'
      }
      this.$router.push({ name: name })
    },
    // 提现消费
    goWithdraw() {
      this.$router.push({ name: 'SurplusWithdrawal' })
    },
    // 快捷时间选择
    selecttime(val) {
      let now = new Date().getTime()
      let now0 = new Date(new Date().toLocaleDateString()).getTime() //当天0点时间戳
      let now1 = new Date(new Date().toLocaleDateString()).getTime() - 24 * 3600 * 1e3 //昨天0点时间戳
      let now7 = new Date(new Date().toLocaleDateString()).getTime() - 7 * 24 * 3600 * 1e3 //7天前0点时间戳
      let now30 = new Date(new Date().toLocaleDateString()).getTime() - 30 * 24 * 3600 * 1e3 //30天前0点时间戳
      let start
      let end = now
      if (!val) {
        start = now0
      } else if (val === 1) {
        start = now1
        end = now0 - 1
      } else if (val === 7) {
        start = now7
      } else if (val === 30) {
        start = now30
      }
      this.params.addtime = start
      this.params.endtime = end
      this.getStateList()
    },
    // 搜索
    search() {
      this.params.page = 1
      this.getStateList()
    },
    // 清空
    clear() {
      this.timeselect = ''
      for (let key in this.params) {
        this.params[key] = ''
      }
      this.params.page = 1
      this.params.size = 10
      this.getStateList()
    },
    // 分页切换
    handleCurrentChange() {
      this.getStateList()
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3)
    },
    statusFormat(row, column, cellValue) {
      let _n = ''
      this.statusList.forEach(item => {
        if (item.id == cellValue) {
          _n = item.name
        }
      })
      return _n
    },
    // 选中
    handleSelection(selection, row) {
      if (this.selectObj[row.suppliers_statement_id]) {
        this.changeSelectState([row], false)
      } else {
        this.changeSelectState([row], true)
      }
    },
    selectAll(row) {
      if (row.length) {
        this.changeSelectState(row, true)
      } else {
        this.changeSelectState(this.list, false)
      }
    },
    changeSelectState(row, flag) {
      const obj = { ...this.selectObj }
      if (flag) {
        row.forEach(item => {
          obj[item.suppliers_statement_id] = item
        })
      } else {
        row.forEach(item => {
          delete obj[item.suppliers_statement_id]
        })
      }
      this.selectObj = { ...obj }
    },
    filterSelect(row) {
      if (this.isSettle) {
        let isSelect = row.status > 1 ? false : true
        return isSelect
      } else {
        return true
      }
    },
    exportList() {
      let list = [...Object.keys(this.selectObj)]
      if (!list.length) {
        return
      }
      exportList({
        suppliers_id: this.suppliers_id,
        suppliers_statement_id: list
      }).then(
        res => {
          const date = this.utils.formatDate('YYYY-MM-DD', new Date().getTime())
          saveAs(new Blob([res.data]), res.filename || this.suppliers_info.suppliers_name + date + '结算单.xls')
        },
        error => {
          console.log(error)
        }
      )
    },
    exportPay() {
      let list = [...Object.keys(this.selectObj)]
      list = list.map(x => Number(x))
      this.saveStatementIdList(list)
      if (!list.length) {
        return
      }
      statementPreview(this.suppliers_id, list).then(
        res => {
          res.all_cash = res.all_cash > 0 ? res.all_cash : 0
          res.all_huanbi = res.all_huanbi > 0 ? res.all_huanbi : 0
          this.saveStatementPreview(res)
          this.$router.push({ name: 'SettlementAdd' })
        },
        error => {
          console.log(error)
        }
      )
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/style/custom.css';
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;

  h2 {
    margin: 5px 5px;
  }
}

.container {
  font-size: 15px;
  .title-item {
    vertical-align: middle;
    display: inline-block;
    margin: auto 20px;
  }
  .account-detail {
    // width: 800px;
    /* & > div {
      width: 49%;
      float: left;
    } */
    .left {
      border-right: 1px solid #eee;
    }
    .detail-title {
      padding: 0 20px;
      text-align: justify;
    }
    .detail-item {
      width: 50%;
      vertical-align: middle;
      display: inline-block;
      // margin: auto 40px;
      h3 {
        text-align: center;
      }
      p {
        font-size: 15px;
        text-align: center;
      }
      span {
        font-size: 0.7em;
        padding-left: 10px;
        color: #409eff;
        cursor: pointer;
        font-weight: normal;
      }
    }
  }
  .search-wrapper {
    background: #fff;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px 0 20px 15px;
    .radio-wrapper {
      display: inline-block;
      padding: 0 4px;
      border-radius: 4px;
      background-color: #eee;
      margin-left: 10px;
    }
  }
  .list-wrapper {
    background: #fff;
    padding: 15px;
    .el-table {
      margin-top: 20px;
    }
  }
  .survey {
    padding: 10px 0;
    display: flex;
    justify-content: center;
    .skybule {
      color: #41a5e1;
      cursor: pointer;
    }
  }
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
